<template>
  <div class="detailHeader">
    <div class="content"></div>
  </div>
</template>

<script>
export default {
  name: "searbox",
  data() {
    return {};
  },
  created() {},
  methods: {}
};
</script>
<style scoped>
.detailHeader{
    background: url('../../static/img/running.gif') no-repeat left center;
    background-size: 120px auto;
    height: 120px;
    animation: running 8s infinite linear ;
}
@keyframes running{
    0%{
       background-position: 0px center; 
    }
    100%{
        background-position: 100% center; 
    }
}
</style>


