import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index'
import vueList from '@/pages/list/vue/vue-list'
import vue01 from '@/pages/detail/vue/vue-01'




Vue.use(Router)
export default new Router({
    mode: "history",
    routes: [{
            path: '/',
            name: 'Index',
            component: Index
        },
        {
            path: '/vueList',
            name: 'vueList',
            component: vueList,
        },
        {
            path: '/vueList/vue01',
            name: 'vue01',
            component: vue01,
        },
    ]
})


