<template>
  <div class="index">
    <div class="webcomBg" v-if="!islook">
      <div class="welcomePages">
        <div class="welcome" @click="comeIn()">欢迎</div>
      </div>
    </div>
    <div class="home" :class="islook?'blockImport':''">
      <div class="content">
        <div class="blogIndex">
          <p class="title">属于自己的一处净土</p>
          <ul class="nav">
            <li class="navList" @click="navTab(1)" :class="navActive==1?'active':''">学习积累</li>
            <li class="navList" @click="navTab(2)" :class="navActive==2?'active':''">生活随笔</li>
          </ul>
          <ul class="learnSubset" v-if="navActive==1">
            <li v-for="(item) in learnSubsetList" :key="item.title">
              <router-link :to="item.url">{{item.title}}</router-link>
            </li>
          </ul>
          <ul class="lifeSubset" v-if="navActive==2">
            <li v-for="(item) in lifeSubsetList" :key="item.title">
              <router-link :to="item.url">{{item.title}}</router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "welcome",
  data() {
    return {
      navActive: 0,
      learnSubsetList: [
        {
          title: "VUE",
          url: "/vueList"
        }
      ],
      lifeSubsetList: [
        {
          title: "自身",
          url: "/lifeList"
        }
      ],
      islook: false
    };
  },
  created() {
    this.islook = sessionStorage.getItem("isLook") || false;
  },
  mounted() {},
  methods: {
    comeIn() {
      let _this = this;
      $(".webcomBg").fadeOut(1500, function() {
        $(".home").fadeIn(1500);
      });
      sessionStorage.setItem("isLook", true);
    },
    navTab(val) {
      this.navActive = val;
    }
  }
};
</script>
<style scoped>
.index {
  height: 100%;
}
.webcomBg {
  height: 100%;
  background: linear-gradient(to bottom, #01cbb9 0%, #ffffff 100%);
}
.welcomePages {
  height: 100%;
  background: url("../../static/img/xkbg.png") repeat-x center bottom;
  background-size: 500px auto;
}
.welcome {
  position: fixed;
  top: 40%;
  left: 50%;
  z-index: 99;
  background: #000;
  color: #fff;
  width: 200px;
  height: 200px;
  border-radius: 100%;
  font-weight: bold;
  animation: myfirst 2s infinite ease-in-out;
  text-align: center;
  line-height: 200px;
  transform: translate(-50%, -50%);
  font-size: 40px;
  transition-duration: 0.4s;
  cursor: pointer;
}
.welcome:hover {
  background: lightseagreen;
  animation: none;
  box-shadow: 0 0 20px #fff;
}
@keyframes myfirst {
  0% {
    width: 200px;
    height: 200px;
    line-height: 200px;
    font-size: 40px;
  }
  25% {
    width: 300px;
    height: 300px;
    line-height: 300px;
    font-size: 70px;
  }
  35% {
    width: 200px;
    height: 200px;
    line-height: 200px;
    font-size: 40px;
  }
  50% {
    width: 270px;
    height: 270px;
    font-size: 60px;
    line-height: 270px;
  }
  75% {
    width: 200px;
    height: 200px;
    font-size: 40px;
    line-height: 200px;
  }
  90% {
    width: 250px;
    height: 250px;
    font-size: 55px;
    line-height: 250px;
  }
  100% {
    width: 200px;
    height: 200px;
    font-size: 40px;
    line-height: 200px;
  }
}
.home {
  display: none;
  height: 100%;
}
.home .content {
    position: relative;
  height: 100%;
}
.home .content .blogIndex{
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 100%;
}
.title {
  font-size: 30px;
  color: #333;
  text-align: center;
  font-weight: bold;
  padding: 0 0 2% 0;
}
.home .nav {
  text-align: center;
  margin: 50px auto  50px auto;
}
.home .nav .navList {
  display: inline-block;
  margin: 0 20px;
  border: 1px solid #ddd;
  padding: 8px 30px;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
  transition-duration: 0.3s;
  box-shadow: 2px 2px 0px #999;
}
.home .nav .navList.active {
  background: #666;
  color: #fff;
}
.home .nav li:hover {
  background: #fff;
}
.home .learnSubset,
.home .lifeSubset {
  width: 800px;
  margin: 0 auto;
  background: #fff;
  border-radius: 10px;
  padding: 20px;
  min-height: 200px;
}
.home .learnSubset li,
.home .lifeSubset li {
  background: #f5f5f5;
  display: inline-block;
  margin: 0 20px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 12px;
  cursor: pointer;
  transition-duration: 0.3s;
  box-shadow: 2px 2px 0px #999;
}
.home .learnSubset li a,
.home .lifeSubset li a {
  display: block;
  height: 100%;
  padding: 8px 30px;
}
.home .learnSubset li:hover,
.home .lifeSubset li:hover {
  background: #fff;
}
</style>


